<template>
  <el-card class="box-card">
    <div slot="header" class="clearfix">
      <span>重置密码</span>
    </div>
    <!-- 表单 -->
    <el-form :model="pwdForm" :rules="pwdFormRules" ref="pwdForm" label-width="100px">
      <el-form-item label="原密码" prop="old_pwd">
        <el-input v-model="pwdForm.old_pwd" type="password"></el-input>
      </el-form-item>
      <el-form-item label="新密码" prop="new_pwd">
        <el-input v-model="pwdForm.new_pwd" type="password"></el-input>
      </el-form-item>
      <el-form-item label="确认新密码" prop="re_pwd">
        <el-input v-model="pwdForm.re_pwd" type="password"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button @click="changePwdHandler" type="primary">修改密码</el-button>
        <el-button @click="rePwdHandler">重置</el-button>
      </el-form-item>
    </el-form>
  </el-card>
</template>

<script>
export default {
  name: 'UserPwd',
  methods: {
    rePwdHandler () {
      this.$refs.pwdForm.resetFields()
    },
    changePwdHandler () {
      this.$refs.pwdForm.validate(async result => {
        if (!result) return
        await this.$http.patch('/my/updatepwd', this.pwdForm)
        this.$refs.pwdForm.resetFields()
      })
    }
  },
  data () {
    const checkSamePwd = (rule, val, callback) => {
      if (val === this.pwdForm.old_pwd) {
        return callback(new Error('新旧密码不能相同'))
      }
      callback()
    }
    const checkRepwd = (rule, val, callback) => {
      if (val !== this.pwdForm.new_pwd) {
        return callback(new Error('两次新密码不一致'))
      }
      callback()
    }
    return {
      // 表单的数据对象
      pwdForm: {
        old_pwd: '',
        new_pwd: '',
        re_pwd: ''
      },
      // 表单的验证规则对象
      pwdFormRules: {
        old_pwd: [
          { required: true, message: '原密码不可以为空！', trigger: 'blur' },
          { pattern: /^\S{6,15}$/, message: '必须时6-15位的文本', trigger: 'blur' }
        ],
        new_pwd: [
          { required: true, message: '新密码不可以为空！', trigger: 'blur' },
          { pattern: /^\S{6,15}$/, message: '必须时6-15位的文本', trigger: 'blur' },
          { validator: checkSamePwd, trigger: 'blur' }
        ],
        re_pwd: [
          { required: true, message: '确认密码不可以为空！', trigger: 'blur' },
          { pattern: /^\S{6,15}$/, message: '必须时6-15位的文本', trigger: 'blur' },
          { validator: checkRepwd, trigger: 'blur' }
        ]
      }
    }
  }
}
</script>

<style lang="less" scoped>
.el-form {
  width: 500px;
}
</style>
